<template>
  <div class="product-detail-options-item">
    <div class="options-selected">
      <strong>{{title}}:</strong>
      <span
        :class="['options-selected-value', hoverIndex != -1 ? 'hover' : '']"
      >
        <span
          v-for="(item, index) in selectList"
          :class="[
            index == selectedIndex ? 'selected' : '',
            index == hoverIndex ? 'hover' : '',
          ]"
          >{{ item.name }}</span
        >
      </span>
      <button v-if="subBtn" class="sub-btn">{{subBtn}}</button>
    </div>
    <div class="product-detail-select">
      <label
        class="product-select"
        v-for="(item, index) in selectList"
        :key="index"
        @mouseover="mouseoverLabel(index)"
        @mouseleave="mouseleaveLabel"
        @click="clickLabel"
      >
        <input type="radio" :disabled="item.disabled" :value="index" v-model="selectedIndex" /><span>{{ item.name }}</span>
      </label>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ProductDetailOptionSelect',
  props: {
    title: String,
    selectList: Array,
    selected: {
      type: Number,
      default: 0
    },
    subBtn: {
      type: String
    }
  },
  data () {
    return {
      selectedIndex: this.selected,
      hoverIndex: -1,
    }
  },
  methods: {
    mouseoverLabel: function (i) {
      if(i != this.selectedIndex) this.hoverIndex = i
    },
    mouseleaveLabel: function () {
      this.hoverIndex = -1
    },
    clickLabel: function() {
      this.hoverIndex = -1
    },
  },
}
</script>

