<template>
  <div class="accordion-wrap">
    <div
      v-for="(item, index) in contents"
      :class="['accordion', activeIndex === index ? 'active' : '']"
    >
      <div
        class="accordion-header"
        v-html="item.title"
        @click="clickHeader(index)"
      ></div>
      <transition
        @before-enter="accBeforeEnter"
        @enter="accEnter"
        @leave="accLeave"
      >
        <div v-show="activeIndex === index" class="accordion-content">
          <div class="accordion-content-inner" v-html="item.content"></div>
        </div>
      </transition>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Accordion',
  props: {
    contents: Array
  },
  data () {
    return {
      activeIndex: -1,
      accTmpHeight: 0
    }
  },
  methods: {
    clickHeader: function (i) {
      if (this.activeIndex === i) {
        this.activeIndex = -1
      } else {
        this.activeIndex = i
      }
    },
    accBeforeEnter: function (el) {
      console.log('accBeforeEnter')
      el.style.display = 'block'
      el.style.height = ''
      this.accTmpHeight = el.clientHeight
      el.style.display = 'none'
      el.style.height = 0
    },
    accEnter: function (el) {
      console.log('accEnter')
      setTimeout(() => {
        el.style.height = this.accTmpHeight + 'px'
      }, 0)
    },
    accLeave: function (el) {
      console.log('accLeave')
      setTimeout(() => {
        el.style.height = '0px'
      }, 0)
    },
  },
}
</script>
