<template>
  <div class="product-detail-options-item">
    <div class="options-selected">
      <strong>컬러:</strong>
      <span
        :class="['options-selected-value', hoverIndex != -1 ? 'hover' : '']"
      >
        <span
          v-for="(item, index) in productColors"
          :class="[
            index == selectedIndex ? 'selected' : '',
            index == hoverIndex ? 'hover' : '',
          ]"
          >{{ item.colorName }}</span
        >
      </span>
    </div>
    <div class="product-detail-color">
      <label
        class="product-color"
        v-for="(item, index) in productColors"
        :key="index"
        @mouseover="mouseoverLabel(index)"
        @mouseleave="mouseleaveLabel"
        @click="clickLabel"
      >
        <input
          type="radio"
          :value="index"
          v-model="selectedIndex"
        /><span class="color"
          ><span
            :style="{
              backgroundColor:
                'rgb(' +
                item.colorCode.r +
                ',' +
                item.colorCode.g +
                ',' +
                item.colorCode.b +
                ')',
            }"
          ></span
        ></span>
        <span class="txt">{{ item.colorName }}</span>
      </label>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ProductDetailOptionColor',
  props: {
    productColors: Array,
    selected: {
      type: Number,
      default: 0
    }
  },
  data () {
    return {
      selectedIndex: this.selected,
      hoverIndex: -1,
    }
  },
  methods: {
    mouseoverLabel: function (i) {
      if (i != this.selectedIndex) this.hoverIndex = i
    },
    mouseleaveLabel: function () {
      this.hoverIndex = -1
    },
    clickLabel: function () {
      this.hoverIndex = -1
    },
  },
}
</script>

